import { createSelector } from 'reselect';

const _getProfile = state => {
    return state.profilePageData.profile
};
const _getProfileStatus = state => {
    return state.profilePageData.status
};
const _getMyId = state => {
    return state.authData.id
};

const _profile = state => _getProfile(state);
const _status = state => _getProfileStatus(state);
const _myId = state => _getMyId(state);

export const getFullProfile = createSelector(
    [_profile, _status, _myId],
    (profile, status, myId) => {
        return {profile, status, myId}
    }
);

export const _getPosts = state => {
    return state.profilePageData.posts
};
export const getPosts = createSelector(
    [_getPosts],
    posts => posts
);
